<template>
  <div class="car">
    <div class="head">
      <div class="in">
        <i></i>
        <p class="p1">我的购物车</p>
        <p class="p2">温馨提示：产品是否购买成功，以最终下单为准哦，请尽快结算</p>
      </div>

    </div>
    <div class="line"></div>
    <div class="carlist banxin">
      <div class="carlist-head">
        <div class="li f2"><input
            type="checkbox"
            class="flex checkbox"
            @click="checkAll"
            v-model="ischeckAll"
          ><span>全选</span></div>
        <div class="li f3"><span>商品名称</span></div>
        <div class="li f1"><span>单价</span></div>
        <div class="li f2"><span>数量</span></div>
        <div class="li f1"><span>小计</span></div>
        <div class="li f1"><span>操作</span></div>
      </div>
      <div
        class="carlist-head"
        v-for="item in list"
        :key="item.id"
        :data-id="item.id"
      >
        <div class="li f2 flex"><input
            type="checkbox"
            class="checkbox"
            v-model="item.check"
            @click="checkItems(item)"
          ><span></span></div>
        <div class="li f3"><span>{{item.productName}}</span></div>
        <div class="li f1"><span>￥{{item.price}}</span></div>
        <div class="li f2"><span class="count f2" @click="add(item)">+</span><input
            type="text"
            class="f1"
            :value="item.num"
          ><span class="count f2" @click="reduce(item)">-</span></div>
        <div class="li f1"><span>￥{{item.price*item.num}}</span></div>
        <div class="li f1"><button @click="del(item)">删除</button></div>
      </div>
      <div class="car-bottom">
        <button @click="sums"> 结算</button>
        
        <span>总计:￥{{sumss}}元</span>
      </div>


    </div>

  </div>
</template>

<script>
import { homeCarApi,updateCarApi,delCarApi,addOrder } from "../http/api";
export default {
  data() {
    return {
      isCheck: false,
      userId: 5400,
      list: [],
      sum: "",
    };
  },
  created() {
    // LoginApi({userName: "ws996", password: "As996996"}).then(res=>{
    //     console.log(res);
    //     this.userId=res.user.user_id
    // })
    // 渲染购物车页面
    homeCarApi({user_id:this.userId}).then((res) => {
      console.log(res);
      if (res.code == "001") {
        this.list = res.shoppingCartData;
        console.log(this.list);

      }
    });
  },
  computed:{
    // 全选状态
    ischeckAll(){
      return this.list.filter(e => e.check).length === this.list.length
    },
    // list过滤器
    listfilter(){
      return this.list.filter(e=>e.check)
    },
    // 总计价格
    sumss(){
      let sumnum=0
      let listzongji=this.listfilter
      console.log(listzongji);
      listzongji.forEach(e=>{
        sumnum+=e.price
      })
      console.log(sumnum);
      return sumnum
    }
      
  },
  methods:{
    // 全选
    checkAll() {
      const res = !this.ischeckAll;
      this.list.forEach(e =>e.check = res);
    },
    // 点选
    checkItems(item) {
      item.check = !item.check;
    },
    // 增加减少商品数量
    add(item){
      if (item.num >= 5) return alert('最多5件')
      item.num++
      // console.log(item);
      updateCarApi({user_id:this.userId,product_id:item.productID,num:item.num}).then(res=>{
        console.log(res);
      })
    },
    reduce(item){
      if (item.num <= 1) return alert('最少1件')
      item.num--
      // console.log(item);
      updateCarApi({user_id:this.userId,product_id:item.productID,num:item.num}).then(res=>{
        console.log(res);
      })
    },
    // 删除购物车
    del(item){
      if(!confirm('是否确认删除？'))return
      delCarApi({user_id:this.userId,product_id:item.productID}).then(res=>{
        console.log(res);
      })
    },
    // 结算，加入订单
    sums(){
      let listadd=[]
      listadd.push(this.list.find(e=>e.check==true))
      addOrder({user_id:this.userId,products:listadd}).then(res=>{
        console.log(res);
      })
      // 提示
      alert('加入购物车成功')
    }
  }
 
};
</script>


<style scoped>
* {
  margin: 0;
  padding: 0;
}
.flex{
  display: flex;
}
.car .li .checkbox{
  margin-top: 24px;
  width: 30px;
}
.line{
  width: 100%;
  height: 1px;
  border: 1px solid #ff6700;
  background-color: #ff6700;
}
.car {
  background-color: #f5f5f5;
  padding-bottom: 30px;
  /* height: 100vh; */
}
.banxin {
  max-width: 1225px;
  margin: 0 auto;
}
.car .head{
  background-color: #fff;
  height: 84px;
}
.car .head .in {
  max-width: 1225px;
  margin: 0 auto;
  background-color: #fff;
  padding: 15px;
  display: flex;
  /* border-bottom: 2px solid #ff6700; */
}
.head .p1 {
  margin-right: 15px;
  font-size: 28px;
  color: #424242;
  overflow: hidden;
}
.head .p2 {
  height: 20px;
  line-height: 60px;
  font-size: 12px;
  color: #757575;
}
.carlist {
  margin-top: 30px;
  width: 100%;
  background-color: #fff;
}
.carlist-head {
  display: flex;
  border-bottom: 1px solid #ccc;
}
.carlist-head .li {
  display: flex;
  padding: 15px 50px;
  height: 85px;
  line-height: 60px;
  /* border-right: 1px solid #000; */
  box-sizing: border-box;
}
.carlist-head .li .count {
  /* display: inline-block; */
  /* line-height: 50px; */
  background-color: #fff;
  padding: 0 5px;
  border: 1px solid #000;
  cursor: pointer;
  text-align: center;
}
.f1{
   flex: 1;
}
.f2{
   flex: 2;
}
.f3{
 flex: 3;
}
.li input{
  width: 50px;
  text-align: center;
  /* height: 30px; */
  /* vertical-align: middle; */
  /* line-height: 50px; */
}
</style>