<template>
  <div class="detail">
      <div class="banxin">
          <div class="header flex">
            <h3>小米CC9 Pro</h3>
            <div class="right">
                <ul class="flex">
                  <li>概述</li>
                  <li>参数</li>
                  <li>用户评价</li>
                </ul>
            </div>
          </div>

          <!-- 商品详情 -->
          <div class="spcenter flex">
            <div class="lefts">
              <!-- 轮播图 -->
               <div class="block">
                  <el-carousel trigger="click">
                    <el-carousel-item v-for="item in arrs" :key="item.id">
                    <h3 class="small"><img :src="item.product_picture | srcFormat" alt=""></h3>
                    </el-carousel-item>
                    </el-carousel>
                       </div>
            </div>
            <!-- 商品描述 -->
            <div class="diburight" v-for="item in arrms" :key="item.id">
              <div class="tou">
              <h1 class="touh1">{{item.product_name}}</h1>
              <p class="toums">{{item.product_intro}}</p>
              <p class="touxm">小米自营</p>
              <div class="toujg flex"><span class="touxm1">{{item.product_selling_price}}元</span><span class="touxm2">{{item.product_price}}元</span></div>
              </div>
              <div class="zhong">
                <div class="zhongs flex">
                  <span class="zhongslx">{{item.product_name}}</span>
                  <span class="zhongslx">
                  <span class="zhongslx1">{{item.product_selling_price}}元</span>
                  <span class="zhongslx2">{{item.product_price}}元</span>
                  </span>
                </div>
                <p class="zhongszj">总计:{{item.product_selling_price}}元</p>
              </div>
              <div class="di">
                <div class="dibtn flex">
                  <div class="dicar" @click="addCar">加入购物车</div>
                  <div class="dilike">喜欢</div>
                </div>
                <ul class="ditext flex">
                  <li>小米自营</li>
                  <li>小米发货</li>
                  <li>7天无理由退货</li>
                  <li>7天价格保护小米自营</li>
                </ul>
              </div>
            </div>
          </div>
      </div>
  </div>
</template>

<script>
import {getGoodsxqApi,getGoodsmsApi,getAddCarApi} from '../http/api'
import "../untils/filters"
export default {
  data(){
    return{
      parmas:{
         productID: this.$route.query.productID
      },
       parmass:{
         product_id: this.$route.query.productID,
         user_id: 5400
      },
      arrs:[],
      arrms:[],
      index : 0,
      i:0,
      s:0,
      num:0
    }
  },
  created(){
    this.getGoodsxq()
    this.getGoodsms()
  },
  methods:{
    getGoodsxq(){
      getGoodsxqApi(this.parmas).then(res=>{
        this.arrs = res.ProductPicture
        console.log(this.arrs);
      })
    },
    getGoodsms(){
      getGoodsmsApi(this.parmas).then(res=>{
        this.arrms = res.Product
      })
    },
    //加入购物车
    addCar(){
      getAddCarApi(this.parmass).then(res=>{
        console.log(res);
      })
    }
  
  
  }  
}
</script>

