<template>
    <div class="allgoods">
      <div class="banxin">
      <Breadcrumb :arr="links"></Breadcrumb>
      <div class="nav flex">
        <span>分类</span>
        <ul class="navul flexs">
          <li class="navli" v-for="item in arr" :key="item.category_id" @click="getGoodsInfo(item)" :class="{active:currentIndex === item.category_id}">{{item.category_name}}</li>
        </ul>
      </div>
  
      </div>
      <div class="goods">
          <div class="banxin">
            <div class="boxs flexs">
            <div class="box1" v-for="item in list" :key="item.id" @click="$router.push(`/goodsdetail?productID=${item.product_id}`)">
              <img :src="item.product_picture | srcFormat" alt="">
              <h2>{{item.product_name}}</h2>
              <h3>{{item.product_title}}</h3>
              <p><span class="p1">{{item.product_selling_price}}元</span> <del class="p2">{{item.product_price}}元</del></p>
            </div>
            </div>
          </div>
          <!-- 分页 -->
      <div class="fy" @click="goodsfy">
        <el-pagination
          background
          layout="prev, pager, next"
          :total="30">
        </el-pagination>
      </div>
      </div>     
  </div>
  
</template>

<script>
import Breadcrumb from "../components/Breadcrumb.vue"
import {GoodsflApi,GoodsInfoApi,getGoodsInfoApi} from "../http/api"
import "../untils/filters"
export default {
  data(){
    return{
      links:[
        {path:"./Home",text:"主页"},
        {path:"",text:"全部商品"},
        {path:"",text:"分类",islist:true}
        ],
      arr:[],
      parmas:{
          categoryID: [],
          currentPage: 1,
          pageSize: 15
        },
      list:[],
      currentIndex : 0
    }
  },
  components:{Breadcrumb},

  created(){
  this.Goodsfl()
  this.GoodsInfo()
  },

  methods:{
    Goodsfl(){
      GoodsflApi().then(res=>{
        this.arr = res.category
        this.arr.unshift({category_id: 0,category_name: "全部"})
      })
    },
    GoodsInfo(){
      GoodsInfoApi(this.parmas).then(res=>{
        this.list = res.Product
      })
    },
  getGoodsInfo(item){
    this.currentIndex = item.category_id
    this.parmas.categoryID = item.category_id
    getGoodsInfoApi(this.parmas).then(res=>{
      this.list = res.Product
    })
    if(this.parmas.categoryID == 0){
      this.parmas.categoryID = ""
      this.GoodsInfo()
    }
  },
    // 分页
  goodsfy(e){
    if(e.target.innerText == "1"){
      this.parmas.currentPage = 1
        this.GoodsInfo()
    }else if(e.target.innerText == "2"){
      this.parmas.currentPage = 2
      this.GoodsInfo()
    }else if(e.target.innerText == "3"){
      this.parmas.currentPage = 3
      this.GoodsInfo()
    }
    if(e.target.className == "el-icon el-icon-arrow-right"){
      this.parmas.currentPage++
      this.parmas.currentPage>3? " ": this.GoodsInfo()
    }else if(e.target.className == "el-icon el-icon-arrow-left"){
      this.parmas.currentPage--
      this.parmas.currentPage<1? " ": this.GoodsInfo()
    }
  }
  }
}
</script>

<style scoped>
.banxin{
    width: 1225px;
    margin: auto;
  }
  .flexs{
    display: flex;
    justify-content: start;
  }
  .nav{
    justify-content: start;
    margin-top: 30px;
    align-items: center;
  }
  .navul li{
    font-size: 14px;
    text-align: center;
    border: 1px solid #e4e7ed;
    padding: 15px 20px;
    cursor: pointer;
  }
  .navul .active{
    color: #409eff;
    border-bottom: none;
  }
  .nav span{
    font-size: 20px;
    padding: 10px 10px;
    font-weight: 700;
  }

  /* 商品 */
  .goods{
    background-color: #f5f5f5;
  }
  .boxs{
    padding-top: 4px;
    flex-wrap: wrap;
    flex: 1;
    min-height: 650px;
  }
  .box1{
    width: 226px;
    height: 300px;
    background-color: #fff;
    margin-right: 10px;
    margin-top: 10px;
    text-align: center;
    cursor: pointer;
  }
  .box1 img{
    height: 60%;
  }
  .box1 h2{
    padding: 10px 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .boxs h3{
    padding-bottom: 20px;
    color: #b0b0b0;
  }
  .boxs .p1{
    color: #ff6700;
  }
  .boxs del{
    color: #b0b0b0;  
  }
  .box1:hover{
    transition: 0.5s;
    box-shadow: 0 0 50px #ccc;
  }
  

  /*分页*/
  .fy{
    text-align: center;
    padding: 20px 0;
    margin-bottom: 20px;
  }

  /* 页脚 */
  .top{
        justify-content: space-evenly;
  }
  
</style>